.menu{
    align-items: center;
    display: flex;
    height: 80px;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 3;
}

.botao__menu{
    align-items: center;
    display: flex;
    justify-content: flex-end;
    margin: 0 auto;
    max-width: 1500px;
    width: 96%;
}

.bar1, .bar2, .bar3 {
    background-color: #1a0c57;
    height: 5px;
    margin: 6px 0;
    width: 35px;
    transition: 0.4s;
}

.change .bar1 {
    transform: rotate(-45deg) translate(-9px, 6px) ;
    -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
}

.change .bar2 {
    opacity: 0;
}

.change .bar3 {
    transform: rotate(45deg) translate(-8px, -8px) ;
    -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
}

@media (min-width: 1200px){
    .menu{
        display: none;
    }
}